import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  // 状态!!!
  state: {
    arr: [],
  },
  getters: {},
  // 突变!!!
  mutations: {
    // 同步的修改操作，少用
    xiunum(state, n,) {
      state.arr = n;
    },
    xiuarr(state, n) {
      state.arr.status = n;
    },
    xiuarrs(state, n) {
      state.arr.status = n;
    },
  },
  // 动作!!!
  actions: {
    // 异步的修改操作，多用
    upnum(context, n) {
      context.commit("xiunum", n); //调用mutations中的修改
    },
    uparr(context, n) {
      context.commit("xiuarr", n); //调用mutations中的修改
    },
    uparrs(context, n) {
      context.commit("xiuarrs", n); //调用mutations中的修改
    },
  },
  modules: {},
});

